@import "index";

body > header, article, body > footer {
  padding: 1.5em; }

@media (min-width: 40em) {
  body > header, article, body > footer {
    padding: 1.5em calc(34% - 12rem); } }



body > header {
  box-shadow: 0 0 .6em rgba(40, 40, 40, .05);
  border-bottom: 1px solid reduce(12); }




